@import "./src/common/styles/sass/variables";

.arrow {
  position: fixed;
  display: none;
  width: 30px;
  height: 30px;
  z-index: 200000;
  border-radius: 50%;
  background-color: $primaryColor;
  top: 93%;
  left: 95%;
  @media screen and (max-width: 860px) {
    left: 90%;
  }

  .icon {
    color: #eeeeee;
    width: 30px;
    height: 30px;
  }
}

.showArrow {
  display: flex;
  align-items: center;
  justify-content: center;
}